import { Component, OnInit } from '@angular/core';
import { ActivatedRoute, Router } from '@angular/router';
import { AlertController } from '@ionic/angular';
import { Socket } from 'ngx-socket-io';
import { Observable } from 'rxjs';
import { TicketsService } from 'src/app/providers/Api/Tickets.service';
import { InicioService } from '../../../providers/Almacenamiento/inicio.service';
import { ToastServices } from '../../../providers/Utilidades/ToastService.service';

@Component({
  selector: 'app-taller-ticket',
  templateUrl: './taller-ticket.page.html',
  styleUrls: ['./taller-ticket.page.scss'],
})
export class TallerTicketPage implements OnInit {

  
  configurando = false;
  usuario = '';
  habilitado = false;
  cargado = false;
  id = '';

  salas = [];

  constructor(
    private almacenamiento: InicioService,
    private toast: ToastServices,
    private apiTickets: TicketsService,
    private alert: AlertController,
    private router: Router,
    private active: ActivatedRoute,
    private socket: Socket
  ) {
    this.escucharProcesados().subscribe((data: any) => {
      if (data.exe) {
        this.salas.forEach((item) => {
          if (item._id === data.data.sala._id) {
              item.ocupada = true;
              item.info = true;
              item.mas = data.data.doc;
          }
        })
      }
    })


    this.escucharFinalizados().subscribe((data: any) => {
      if (data.exe) {
        this.salas.forEach((item) => {
          if (item._id === data.data._id) {
              item.ocupada = false;
              item.info = false;
          }
        })
      }
    })
   }

  ngOnInit() {
  }

  ionViewWillEnter() {
    this.disponibilidad();
    this.almacenamiento.insertarTicket('entrando').then((r) => {
      if (r) {
        return true;
      }
    });
  }

  refrescando(event) {
    this.salas = [];
    this.disponibilidad().then(() => {
        event.detail.complete();
    }).catch(() => {
      event.detail.complete();
    })
  }

  async disponibilidad() {
    const info = await this.almacenamiento.obtenerToken();

    if ( info ) {
      this.usuario = info.nombre;
      this.apiTickets.disponibilidadnegocio(info.token, {id: this.active.snapshot.paramMap.get('id')}).subscribe((data: any) => {
        // console.log(data)
        if (data.exe) {
          this.cargado = true;
            this.habilitado = data.data[0].estado;
            this.id = data.data[0]._id;
          if (data.response === 'no') {
            this.toast.toastNotificacion('No hay disponibilidad en este momento')
            this.misSalas();
          } else {
            
            this.misSalas();
          }
        } else {
          this.toast.toastError('No se pudo consultar la disponibilidad, inténtelo más tarde');
          this.router.navigate(['/central/menu/'])
        }
      }, (err) => {
        this.toast.toastError('No se pudo consultar la disponibilidad, inténtelo más tarde');
        this.router.navigate(['/central/menu/'])
      })
    }
  }



  async misSalas() {
    this.configurando = true;
    const info = await this.almacenamiento.obtenerToken();

    if ( info ) {
      this.apiTickets.salasNegocio(info.token, {id: this.active.snapshot.paramMap.get('id')}).subscribe((data: any) => {
        if (data.exe) {
          data.response.forEach((item) => {
            item.info = false;
            this.socket.emit('ticket', {id: item.creador})
            return new Promise((resolve, reject) => {
              if (item.ocupada) {
                this.apiTickets.ticketsolo(info.token, {id: item.ticket._id}).subscribe((data2: any) => {
                  // console.log(data2, ' mi data')
                  if(data2.exe) {
                    item.info = true;
                    item.mas = data2.response;
                    resolve(true)
                  } else {
                    item.info = false;
                    resolve(false)
                  }
  
                }, (err) => {
                  item.info = false;
                  resolve(false)
                });
              }
             
            })
          })
          this.salas = data.response;
          // console.log('salas', data)
        } else {
          this.toast.toastError('No se pudo traer las salas, inténtelo más tarde');
          this.router.navigate(['/central/menu/'])
        }
        this.configurando = false;
      }, (err) => {
        this.toast.toastError('No se pudo traer las salas, inténtelo más tarde');
        this.router.navigate(['/central/menu/'])
        this.configurando = false;
      })
    }
  }


  escucharProcesados() {
    const myobserver = new Observable(observer => {
      this.socket.on('procesar', (data) => {
        observer.next(data);
      });
    });
  
    return myobserver;
  }


  escucharFinalizados() {
    const myobserver = new Observable(observer => {
      this.socket.on('finalizar', (data) => {
        observer.next(data);
      });
    });
  
    return myobserver;
  }

  ionViewWillLeave() {
    this.almacenamiento.eleminarTicket().then((r) => {
      if (r) {
        return true;
      }
    });
  }

}


